<template>
  <div id="account">
    <common-header title="安全中心"></common-header>
    <Cell title="手机号码" @click="linkTo('Code')"/>
    <!--<Cell title="登录密码" @click="linkTo('EditPassword')"/>-->
    <Cell title="支付密码" @click="linkTo('EditPay')"/>
  </div>
</template>

<script>
  import Cell from '@/components/Cell/Cell';

  export default {
    components: {
      Cell
    }
  }
</script>

<style lang="scss">
  #account {
    height: 100%;
    background: #f0f0f0;
  }
</style>


